body {
    padding: 0;
    margin: 0;
}

#deck-view {
    display: flex;
    flex-direction: column;

    height: 100vh;
    max-width: 100vw;
    /* grid-template-rows: 75px auto;
    grid-template-areas:
        "navbar"
        "content"; */
}

#navbar {
    flex: 0 0 60px;

    display: flex;
    flex-direction: row;

    align-items: center;
    justify-content: flex-start;

    width: 100%;

    background-color: rgb(0, 0, 0);
}

.navbar-menu {
    padding-left: 0.5em;
    padding-right: 0.5em;
    order: 0;
    /* flex: 0 0 auto; */
}

.navbar-menu button {
    height: 50px;
    
    border-radius: 0;
    background-color: black;
    border-width: 0;
    color: white;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 1em;
}

.navbar-header {
    padding-left: 0.5em;
    padding-right: 0.5em;
    color: white;
    order: 1;
    /* flex: 1 1 auto; */
    
    flex: auto;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.navbar-social {
    padding-left: 0.5em;
    padding-right: 0.5em;
    order: 2;
    margin-left: auto;
    /* flex: 0 0 auto; */

    color: white;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

#deck-container {
    padding-top: 5px;
    padding-bottom: 5px;
    flex-grow: 1;
    position: relative;

    display: flex;
    flex-direction: column;

    align-items: center;
    justify-content: center;
    background-color: rgb(236, 236, 236);
}

#deck-carousel {
    display: flex;
    flex-direction: row;

    scroll-snap-type: x mandatory;
    scroll-padding-left: 50px;

    margin-top: 5px;
    margin-bottom: 5px;

    justify-content: flex-start;
    overflow-x: auto;

    max-width: 100%;
}

.deck {
    scroll-snap-align: start;
    position: relative;
    display: grid;

    padding: 5px;

    margin-left: 50px;
    margin-right: 50px;
}

.deck h2 {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    justify-self: center;
}

.deck-card {
    position: relative;
    aspect-ratio: 1;
    height: max(100px, 20vh);

    box-shadow: 2px 2px 6px rgb(73, 73, 73);
    background-color: white;
    
    border-radius: 10px;
    
    z-index: 2;
}

